<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
            <table-component
                    ref="table"
                    @handleOperate="handleOperate"
                    :columns="columns"
                    :request="tableRequest">
            </table-component>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import {getDepartmentList, deleteDepartment} from '@/api/department'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, filterComponent, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '名称',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: '上级党支部名称',
                        dataIndex: 'parent.name',
                        key: 'parent.name',
                    },
                    {
                        title: '书记',
                        dataIndex: 'secretary',
                        key: 'secretary',
                    },
                    {
                        title: '成立日期',
                        dataIndex: 'established_at',
                        key: 'established_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable: false,},
                            delete: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getDepartmentList,
                filterFields:[
                    {label:'上级党支部',field:'department_id'},
                    {label:'成立时间',field:'start_timestamp'},
                    {label:'-',field:'end_timestamp'},
                    {label:'关键字',field:'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleEdit(id) {
                this.$router.push({name: '党支部信息编辑', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteDepartment(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>
